// @mui
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { Button, Modal, TextField, Grid, Switch, FormControlLabel, Menu, MenuItem, FormControl, InputLabel, Select } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { color, decryptToken, formatObj } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import DataTable from 'react-data-table-component';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';

// ----------------------------------------------------------------------

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '70%',
    bgcolor: 'background.paper',
    borderRadius: '10px',
    boxShadow: 24,
    p: 2,
    backgroundColor: color.modalbackground,
    height: 'auto',
    overflow: 'auto',
};


const initialBankState = {
    id: '',
    accountNumber: '',
    ifscCode: '',
    bankName: '',
    branchName: '',
    isDefault: false
};

const initialTransferState = {
    fromAccount: '',
    toAccount: '',
    amount: '',
    transferDate: null
};


export default function UserBankingDetails() {
    const settings = useSettingsContext();
    const { enqueueSnackbar } = useSnackbar();
    const [open, setOpen] = useState(false);
    const [bank, setBank] = useState(initialBankState);
    const [banks, setBanks] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const [confirmBankAccountNumber, setConfirmBankAccountNumber] = useState('');
    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);
    const [tempData, setTempData] = useState({});
    const [deleteModal, setDeleteModal] = useState(false);
    const [transferModal, setTransferModal] = useState(false);


    const [bankTransfer, setBankTransfer] = useState(initialTransferState);

    const handleTransferBankChange = (e) => {
        const { name, value } = e.target;
        setBankTransfer((prevBank) => ({
            ...prevBank,
            [name]: value,
        }));

    };

    const handleBankAmountTransfer = () => {

        const transferPayload = {
            fromAccount: bankTransfer.fromAccount,
            toAccount: bankTransfer.toAccount,
            amount: bankTransfer.amount,
            transferDate: dayjs(bankTransfer.transferDate).format('YYYY-MM-DD')
        };

        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        axios.post(endpoints.bank.transfer, transferPayload,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Bank transfer successful!', { variant: 'success' });
                // Reset the product  state
                setBankTransfer(initialTransferState);
                setTransferModal(false);
                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error, { variant: 'error' });
            })
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setBank((prevBank) => ({
            ...prevBank,
            [name]: (name === 'ifscCode' || name === 'bankName' || name === 'branchName') ? value.toUpperCase() : value,
        }));

    };

    const handleSaveBankDetails = () => {
        console.log({ bank })
        // If no errors, proceed with saving
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        axios.post(endpoints.bank.add, bank,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Bank added successfully!', { variant: 'success' });
                // Reset the product  state
                setBank(initialBankState);
                setConfirmBankAccountNumber('');
                fetchData();
                setOpen(false);
            }).catch((err) => {
                enqueueSnackbar(err.error, { variant: 'error' });
            })
    };

    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.bank.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setBanks(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch bank accounts!', { variant: 'error' });
            setIsLoading(false);
        }
    };

    useEffect(() => {

        setIsLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const handleDeleteDetails = () => {
        setBank((prev) => ({ ...prev, id: tempData._id, bankName: tempData.bankName, accountNumber: tempData.accountNumber, ifscCode: tempData.ifscCode }));
        setAnchorEl(null);
        setDeleteModal(true);
    };

    const handleConfirmDeleteBank = () => {
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        axios.post(endpoints.bank.delete, { id: bank.id },
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Bank deleted successfully!', { variant: 'success' });
                // Reset the product  state
                setBank(initialBankState);
                setDeleteModal(false);
                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error, { variant: 'error' });
            })
    };

    const columns = [
        {
            name: 'Set as Default',
            selector: (row) => row.isDefault,
            cell: (row) => <Switch
                checked={row.isDefault}
                onChange={(event) => {
                    setIsLoading(true);
                    const decryptedToken = localStorage.getItem(constants.keyUserToken);
                    const accessToken = decryptToken(decryptedToken);
                    axios.post(endpoints.bank.setAsDefault, { id: row._id },
                        {
                            headers: {
                                Authorization: `Bearer ${accessToken}`,
                            }
                        })
                        .then((res) => {
                            enqueueSnackbar('Default bank updated successfully!', { variant: 'success' });
                            fetchData();
                            setIsLoading(false);
                        }).catch((err) => {
                            enqueueSnackbar(err.error, { variant: 'error' });
                            setIsLoading(false);
                        })
                }}
            />,
            width:'160px'

        },
        {
            name: 'Bank Details',
            selector: (row) => row.branchName,
            cell: (row) => (
                <div>
                    <Typography variant="h6">{row.bankName}</Typography>
                    <Typography variant="subtitle1">{row.accountNumber}</Typography>
                    <Typography variant="subtitle2">{row.ifscCode}</Typography>
                </div>
            ),
            width:'auto'
        },
        {
            name: 'Balance',
            selector: (row) => formatObj.format(row.balance),
        },
        {
            name: 'Actions',
            width: 'auto',
            cell: (row) => (
                <>
                    <Button onClick={(event) => {
                        setAnchorEl(event.currentTarget);
                        setTempData(row)
                    }} style={{ color: color.actionButton }} c variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={() => { }}>Edit Details</MenuItem>
                        <MenuItem onClick={() => handleDeleteDetails()}>Remove</MenuItem>
                    </Menu>
                </>
            ),
        },

    ];

    // async function verifyIFSC(ifscCode) {
    //     try {
    //         const response = await axios.get(`https://ifsc.razorpay.com/${ifscCode}`);
    //         if (response.data) {
    //             return response.data;
    //         } else {
    //             return "IFSC code not found";
    //         }
    //     } catch (error) {
    //         return "Error verifying IFSC code";
    //     }
    // }

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Typography variant="h4">Bank Details </Typography>

            <Box sx={{ mt: 3, p: 2, display: 'flex', gap: '30px', flexDirection: 'column' }}>

                <Box sx={{ display: 'flex', gap: '10px', }}>
                    <Button startIcon={<AddIcon />} variant='contained' color='primary' onClick={() => { setOpen(true) }}>
                        NEW BANK DETAILS
                    </Button>
                    {
                        banks.length > 1 &&
                        <Button startIcon={<AddIcon />} variant='contained' onClick={() => {
                            setTransferModal(true);
                        }}>
                            TRANSFER FUNDS
                        </Button>
                    }

                </Box>

                <Box sx={{ display: 'flex', gap: '10px', border: '1px solid black', borderRadius: '10px' }}>
                    <DataTable
                        data={banks}
                        columns={columns}
                        progressPending={isLoading}
                    />
                </Box>
            </Box>

            <Modal
                open={open}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>

                        <Typography id="modal-modal-title" variant="h4" component="h2">
                            Bank Details
                        </Typography>
                        <Grid container spacing={2} >
                            <Grid item xs={12}>
                                <TextField
                                    sx={{ borderRadius: '7px', backgroundColor: 'white' }}
                                    label='Account Number'
                                    name='accountNumber'
                                    type='number'
                                    value={bank.accountNumber}
                                    onChange={(e) => handleChange(e)}
                                    fullWidth
                                    required />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    sx={{ borderRadius: '7px', backgroundColor: 'white' }}
                                    label='Confirm Bank Account Number'
                                    type='number'
                                    value={confirmBankAccountNumber}
                                    onChange={(e) => setConfirmBankAccountNumber(e.target.value)}
                                    fullWidth
                                    required
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    sx={{ borderRadius: '7px', backgroundColor: 'white' }}
                                    label='IFSC Code'
                                    name='ifscCode'
                                    value={bank.ifscCode}
                                    onChange={(e) => handleChange(e)}
                                    fullWidth
                                    required
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    sx={{ borderRadius: '7px', backgroundColor: 'white' }}
                                    label='Bank Name'
                                    name='bankName'
                                    value={bank.bankName}
                                    onChange={(e) => handleChange(e)}
                                    fullWidth
                                    required
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    sx={{ borderRadius: '7px', backgroundColor: 'white' }}
                                    label='Branch Name'
                                    name='branchName'
                                    value={bank.branchName}
                                    onChange={(e) => handleChange(e)}
                                    fullWidth
                                    required
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel control={<Switch
                                    checked={bank.isDefault}
                                    onChange={(event) => {
                                        setBank((prev) => ({ ...prev, isDefault: !bank.isDefault }));
                                    }}
                                />} label="Make Default" />

                            </Grid>

                        </Grid>
                    </div>
                    <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'flex-end', padding: "10px" }}>

                        <Button variant='outlined' onClick={() => {
                            setBank(initialBankState);
                            setConfirmBankAccountNumber('');
                            setOpen(false);
                        }}>
                            Cancel
                        </Button>
                        <Button variant='contained'
                            color='primary'
                            disabled={
                                !bank.accountNumber ||
                                !bank.ifscCode ||
                                !bank.bankName ||
                                !confirmBankAccountNumber ||
                                !bank.branchName ||
                                (bank.accountNumber !== confirmBankAccountNumber)
                            }
                            onClick={handleSaveBankDetails}
                        >
                            SAVE
                        </Button>
                    </div>
                </Box>

            </Modal>

            {/* Delete Modal */}
            <Modal
                open={deleteModal}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>

                        <Typography id="modal-modal-title" variant="h4" component="h2">
                            Bank Details
                        </Typography>
                        <div style={{ backgroundColor: 'white', padding: '10px', borderRadius: '10px' }}>
                            <div style={{ display: 'flex', flexDirection: 'row', gap: "10px", marginBottom: '10px' }}>
                                <TextField
                                    label='Bank Name'
                                    name='bankName'
                                    value={bank.bankName}
                                    disabled
                                    fullWidth
                                    required
                                />
                                <TextField
                                    label='Account Number'
                                    name='accountNumber'
                                    type='number'
                                    value={bank.accountNumber}
                                    disabled
                                    fullWidth
                                    required />
                            </div>


                            <Grid item xs={6}>
                                <TextField
                                    label='IFSC Code'
                                    name='ifscCode'
                                    value={bank.ifscCode}
                                    disabled
                                    fullWidth
                                    required
                                />
                            </Grid>

                        </div>
                    </div>
                    <Typography sx={{ color: 'red' }}>Note : Deleting the bank account from here will remove the bank details from all existing invoices. Deleted banks cannot be used again and this action cannot be undone. </Typography>
                    <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'flex-end', padding: "10px" }}>
                        <Button variant='outlined' onClick={() => {
                            setBank(initialBankState);
                            setDeleteModal(false);
                        }}>
                            CLOSE
                        </Button>
                        <Button variant='contained'
                            color='primary'
                            onClick={handleConfirmDeleteBank}
                        >
                            DELETE
                        </Button>
                    </div>
                </Box>

            </Modal>

            {/* Transfer Modal */}
            <Modal
                open={transferModal}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>

                        <Typography id="modal-modal-title" variant="h4" component="h2">
                            Internal Bank to Bank Fund Transfer
                        </Typography>
                        <Typography id="modal-modal-title" sx={{ color: 'black' }}>
                            Note : This Transfer only affects the internal bank balances in the software and does not affect the actual bank balances.
                        </Typography>
                        <div style={{ backgroundColor: 'white', padding: '10px', borderRadius: '10px' }}>
                            <div style={{ display: 'flex', flexDirection: 'row', gap: "10px", marginBottom: '10px' }}>
                                <FormControl fullWidth required>
                                    <InputLabel id="demo-simple-select-label">Select From Bank Account</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={bankTransfer.fromAccount}
                                        label="Select From Bank Account"
                                        name='fromAccount'
                                        onChange={handleTransferBankChange}
                                        required
                                    >
                                        {
                                            banks.filter(bankAccount => bankAccount._id !== bankTransfer.toAccount).map((bankAccount) => (
                                                <MenuItem value={bankAccount._id}>{bankAccount.bankName}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </FormControl>
                                <FormControl fullWidth required>
                                    <InputLabel id="demo-simple-select-label">Select To Bank Account</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={bankTransfer.toAccount}
                                        label="Select To Bank Account"
                                        name='toAccount'
                                        onChange={handleTransferBankChange}
                                        required
                                    >
                                        {
                                            banks.filter(bankAccount => bankAccount._id !== bankTransfer.fromAccount).map((bankAccount) => (
                                                <MenuItem value={bankAccount._id}>{bankAccount.bankName}</MenuItem>
                                            ))
                                        }
                                    </Select>
                                </FormControl>

                            </div>

                            <div style={{ display: 'flex', flexDirection: 'row', gap: "10px", marginBottom: '10px' }}>
                                <TextField
                                    label='Enter Amount'
                                    name='amount'
                                    type='number'
                                    value={bankTransfer.amount}
                                    onChange={handleTransferBankChange}
                                    fullWidth
                                    required
                                />

                                <LocalizationProvider dateAdapter={AdapterDayjs} >
                                    <DatePicker
                                        label="Transfer Date"
                                        value={bankTransfer.transferDate}
                                        onChange={(newValue) => {
                                            setBankTransfer((prev) => ({ ...prev, transferDate: newValue }))
                                        }}
                                        format="DD/MM/YYYY"
                                        slotProps={{ textField: { fullWidth: true } }}
                                    />
                                </LocalizationProvider>
                            </div>


                        </div>
                    </div>
                    <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'flex-end', padding: "10px" }}>

                        <Button variant='contained'
                            color='primary'
                            onClick={handleBankAmountTransfer}
                            disabled={
                                !bankTransfer.fromAccount ||
                                !bankTransfer.toAccount ||
                                !bankTransfer.amount ||
                                !bankTransfer.transferDate
                            }
                        >
                            TRANSFER FUNDS
                        </Button>
                        <Button variant='outlined' onClick={() => {
                            setBankTransfer(initialTransferState);
                            setTransferModal(false);
                        }}>
                            CLOSE
                        </Button>
                    </div>
                </Box>

            </Modal>

        </Container>
    );
}
